.home {
  &__select {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 60px;
  }

  &__middle {
    padding-bottom: 50px;
  }

  &__card-title {
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
  }

  &__scroll-img {
    background-color: #d8e1f3;
    padding: 40px 0 30px 50px;
    display: flex;
    gap: 20px;
    overflow-x: hidden;
    white-space: nowrap;
  }
}

.products {
  display: grid;
  grid-template-columns: auto auto auto auto;
  justify-content: space-between;
  gap: 30px;
  flex-direction: column;
  padding-bottom: 30px;
}
