.top {
  box-shadow: rgb(195, 191, 191) 0px 0px 8px 0px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1140px;

  &__left {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 700;
  }

  &__left-text {
    max-width: 152px;
    font-size: 15px;
    line-height: 17px;
    cursor: pointer;
  }

  &__list {
    width: 425px;
    display: flex;
    justify-content: space-between;
    list-style-type: none;
  }

  &__item {
    color: black;
    font-weight: 500;
    font-size: 15px;
    text-decoration: none;
  }

  &__lang {
    width: 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__lang-item {
    color: #4a4a4a;
    cursor: pointer;
    font-weight: 500;
    font-size: 14px;
  }

  &__lang-border {
    display: block;
    width: 2px;
    height: 15px;
    background-color: #4e72ca;
  }

  &__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 180px;
  }

  &__btn {
    background-color: #fff;
    border: none;
    color: #4e72ca;
    font-weight: 700;
    line-height: 21px;
    cursor: pointer;
    text-decoration: none;
  }

  &__btn-profile {
    background-color: #fff;
    border: none;
    color: #4e72ca;
    font-weight: 700;
    line-height: 21px;
    cursor: pointer;
    text-decoration: none;
    color: #4e72ca;
  }

  &__btn-close {
    text-decoration: none;
    color: #4e72ca;
  }

  &__btn:active {
    background-color: rgb(237, 231, 231);
  }

  &__right-border {
    display: block;
    margin-top: 20px;
    background-color: #4e72ca;
    height: 15px;
    width: 2px;
  }
}
