.footer {
  background-color: #33383c;
  padding-top: 30px;

  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__inner-bottom-border {
    display: block;
    background-color: #4c565e;
    height: 1px;
    width: auto;
  }

  &__bottom-list {
    display: flex;
    padding-top: 25px;
    padding-bottom: 25px;
    justify-content: space-between;
    color: #66717a;
  }
}
