.footertwo {
  &.white {
    background-color: white;
  }

  &.black {
    background-color: #282b30;

    .footertop__btn {
      background-color: #282b30;
    }
  }
}

.footertop {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1140px;
  padding-top: 22px;
  padding-bottom: 22px;

  &__text {
    max-width: 307px;
    font-weight: 500;
    font-size: 15px;
    color: #98a4b7;
    line-height: 18px;
  }

  &__left {
    display: flex;
    align-items: center;
    gap: 15px;
    font-weight: 700;
  }

  &__left-text {
    max-width: 152px;
    font-size: 15px;
    line-height: 17px;
    color: white;
    cursor: pointer;
  }

  &__right {
    display: flex;
    gap: 5px;
  }

  &__btn {
    border: none;
    border-radius: 30px;
    background-color: white;
    padding: 0;
    margin: 0;
    cursor: pointer;
  }

  &__storebtn {
    background-image: url(../../../assets/icons/rounded.svg);
    background-repeat: no-repeat;
    background-position-x: calc(3px);
    background-position-y: calc(5px);
  }
}
