.pageProfile {
  background-color: #e7ece9;
  padding-bottom: 80px;
}

.profile {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__top-right {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__name {
    font-weight: 700;
    font-size: 17px;
    line-height: 21px;
    color: #414d6a;
  }

  &__money {
    color: #304fff;
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
  }

  &__plusbtn {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 0;
    font-size: 16px;
    padding-right: 10px;
    border: 1px solid #41d481;
    border-top-left-radius: 28px;
    border-bottom-left-radius: 30px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    background-color: white;
    cursor: pointer;
  }

  &__plusbtn:active {
    background-color: #e7ece9;
  }

  &__plus-img {
    margin: 0;
    padding: 10px;
    background-color: #11b463;
    border-radius: 50%;
  }

  &__navs {
    display: flex;
    padding: 0;
  }

  &__infos {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  &__money-info {
    display: flex;
    padding: 7px 0;
    gap: 250px;
  }

  &__money-infoleft {
    display: flex;
    gap: 70px;
    margin-left: 10px;
  }
  &__money-inforight {
    display: flex;
    gap: 100px;
  }
}
