.profilecomp {
  background-color: white;
  padding: 15px 10px;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__text {
    color: #414d6a;
    font-size: 17px;
    font-weight: 500;
  }

  &__cost {
    font-size: 15px;
    font-weight: 500;
    color: #414d6a;
  }

  &__date {
    font-weight: 500;
    font-size: 14px;
    color: #414d6a;
  }

  &__btn1 {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 0;
    font-size: 16px;
    padding-right: 10px;
    border: 1px solid #41d481;
    border-top-left-radius: 28px;
    border-bottom-left-radius: 30px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    background-color: white;
    cursor: pointer;
  }

  &__btn1:active {
    background-color: rgb(241, 238, 238);
  }

  &__btn2 {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 0;
    font-size: 16px;
    padding-right: 10px;
    border: 1px solid #304fff;
    border-top-left-radius: 28px;
    border-bottom-left-radius: 30px;
    border-top-right-radius: 7px;
    border-bottom-right-radius: 7px;
    background-color: white;
    cursor: pointer;
  }

  &__btn2:active {
    background-color: rgb(241, 238, 238);
  }

  &__burgerbtn {
    border: none;
    background-color: white;
    cursor: pointer;
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    width: 422px;
  }

  &__bottom-text {
    color: #304fff;
  }
}
