.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1230px;
  margin: auto;
  padding-top: 45px;
  padding-bottom: 35px;

  &__list {
    display: flex;
    justify-content: space-between;
    width: 1140px;
  }

  &__next-btn {
    border: none;
    background-color: white;
    cursor: pointer;
  }
}
