.hero {
  display: flex;
  align-items: center;

  &__inner {
    display: flex;
    width: 872px;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #979797;
    border-radius: 22px;
    border-right: none;
    padding: 0;
    padding-left: 10px;
  }

  &__middle-border {
    width: 3px;
    height: 44px;
    background-color: #979797;
    display: block;
  }

  &__middle-btn {
    border: none;
    background-color: white;
    cursor: pointer;
  }

  &__middle-btn2 {
    width: 78px;
    height: 44px;
    color: white;
    padding-top: 13px;
    padding-bottom: 13px;
    border-radius: 20px;
    border: none;
    background-color: #19d476;
    cursor: pointer;
  }

  &__middle-btn2:active {
    background-color: #35b977;
  }

  &__right-btn {
    margin: 0;
    display: flex;
    align-items: center;
    gap: 5px;
    border-radius: 20px;
    border: none;
    background-color: #19d476;
    padding: 0;
    padding-right: 15px;
    color: white;
    font-weight: 700;
    cursor: pointer;
  }

  &__right-btn:active {
    background-color: #11b463;
  }

  &__right-btn-img {
    margin: 0;
    padding: 15px;
    background-color: #11b463;
    border-radius: 50%;
  }
}
