body {
  box-sizing: border-box;
  margin: auto;
  padding: auto;
  font-family: " Monospace";
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

img {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1140px;
  padding: auto;
  margin: auto;
}
