.login {
  display: flex;
  align-items: center;
  gap: 30px;
  padding-bottom: 50px;

  &__left {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__form {
    display: flex;
    flex-direction: column;
    width: 329px;
  }

  &__left-title {
    font-weight: 700;
    line-height: 29px;
    margin-bottom: 30px;
  }

  &__left-text {
    max-width: 334px;
    height: 82px;
    font-size: 17px;
    font-weight: 500;
    color: #304fff;
    text-align: center;
    margin-bottom: 20px;
  }

  &__input {
    height: 62px;
    border: 1px solid #979797;
    border-radius: 5px;
    margin-bottom: 35px;
    margin-top: 15px;
  }

  &__submitBtn {
    padding: 20px 140px;
    background-color: #41d481;
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 5px;
    margin-bottom: 140px;
  }

  &__submitBtn:active {
    background-color: #60e298;
  }

  &__left-bottom {
    display: flex;
    align-items: center;
  }

  &__left-bottomText {
    display: block;
    padding: 6px 13px;
  }

  &__bottom-border {
    display: block;
    width: 140px;
    height: 3px;
    background-color: #979797;
  }

  &__bottomBtn {
    border: none;
    cursor: pointer;
    padding: 10px;
    background-color: white;
    color: #4e72ca;
    font-size: 16px;
    font-weight: 700;
  }

  &__right {
    width: 654px;
    height: 852px;
    background-image: url(../../assets/bgImage/login-bg.png);
  }

  &__right-inner {
    background-repeat: no-repeat;
    padding-top: 80px;
    padding-left: 40px;
    margin-top: 450px;
    margin-left: 60px;
    width: 275px;
    height: 275px;
    background-image: url(../../assets/bgImage/login-inner-bg.png);
  }

  &__right-text {
    width: 213px;
    font-weight: 700;
    font-size: 19px;
    color: #787993;
    margin-bottom: 20px;
  }

  &__right-btn {
    padding: 10px 18px;
    background-color: white;
    border: 2px solid #41d481;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 700;
    color: #41d481;
    cursor: pointer;
  }

  &__right-btn:active {
    background-color: rgb(237, 233, 233);
  }
}
label {
  color: #979797;
}
