.productpage {
  &__title {
    font-size: 34px;
    font-weight: 500;
    line-height: 41px;
    color: #414d6a;
    max-width: 1134px;
    margin-bottom: 40px;
  }

  &__text {
    color: #444444;
    padding: 10px;
  }

  &__infos {
    display: flex;
    gap: 50px;
    padding-bottom: 40px;
  }

  &__right-title {
    font-size: 18px;
    font-weight: 500;
    color: #4e72ca;
    margin-bottom: 30px;
  }

  &__right-price {
    font-size: 55px;
    font-weight: 500;
    line-height: 25px;
  }

  &__right-type {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 40px;
    color: #444444;
    margin-top: 30px;
  }

  &__btns {
    display: flex;
    gap: 20px;
    margin-bottom: 50px;
  }

  &__btn {
    height: 44px;
    color: white;
    padding: 13px;
    border-radius: 20px;
    border: none;
    background-color: #19d476;
    cursor: pointer;
  }

  &__btn:active {
    background-color: #15ae61;
  }

  &__right-text {
    font-size: 20px;
    line-height: 28px;
    color: #5c5555;
  }
}
